<template>
    <div class="page-owasp-wrap">
        <el-row type="flex" class="row-bg" justify="space-between">
            <el-col :span="24">
                <el-row type="flex" class="row-bg" justify="space-between">
                    <el-col :span="12">
                        <h3>Web防护引擎配置</h3>
                    </el-col>
                    <el-col :span="12" class="text-align-right">
                        <a class="el-button el-button--primary is-plain" v-if="ruleType == 'single_rule'"
                            :href="'/#/protection/' + domain + '/' + ruleType">返回</a>
                        <a class="el-button el-button--primary is-plain" v-if="ruleType == 'group_rule'"
                            :href="'/#/group-protection/' + domain ">返回</a>
                    </el-col>
                </el-row>
            </el-col>
        </el-row>
        <el-divider />
        <div class="margin-4x"></div>
        <el-row v-loading.fullscreen.lock="loadingPage">
            <el-col :span="24">
                <el-form :model="webEngineForm" :rules="rules" ref="webEngineForm" label-width="160px"
                    class="engine-form">

                    <el-form-item label="SQL注入防护">
                        <el-radio-group v-model="webEngineForm.sql_check">
                            <el-radio label="block">阻断请求</el-radio>

                            <el-radio label="reject_response">拒绝响应</el-radio>
                            <el-radio label="watch">观察</el-radio>
                            <el-radio label="close">关闭</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="XSS防护">
                        <el-radio-group v-model="webEngineForm.xss_check">
                            <el-radio label="block">阻断请求</el-radio>

                            <el-radio label="reject_response">拒绝响应</el-radio>
                            <el-radio label="watch">观察</el-radio>
                            <el-radio label="close">关闭</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="命令注入防护">
                        <el-radio-group v-model="webEngineForm.command_inject_check">
                            <el-radio label="block">阻断请求</el-radio>

                            <el-radio label="reject_response">拒绝响应</el-radio>
                            <el-radio label="watch">观察</el-radio>
                            <el-radio label="close">关闭</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="WebShell上传防护">
                        <el-radio-group v-model="webEngineForm.webshell_update_check">
                            <el-radio label="block">阻断请求</el-radio>

                            <el-radio label="reject_response">拒绝响应</el-radio>
                            <el-radio label="watch">观察</el-radio>
                            <el-radio label="close">关闭</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="敏感文件泄露防护">
                        <el-radio-group v-model="webEngineForm.sensitive_file_check">
                            <el-radio label="block">阻断请求</el-radio>

                            <el-radio label="reject_response">拒绝响应</el-radio>
                            <el-radio label="watch">观察</el-radio>
                            <el-radio label="close">关闭</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="路径穿越防护">
                        <el-radio-group v-model="webEngineForm.path_traversal_check">
                            <el-radio label="block">阻断请求</el-radio>

                            <el-radio label="reject_response">拒绝响应</el-radio>
                            <el-radio label="watch">观察</el-radio>
                            <el-radio label="close">关闭</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="高危Nday防护">
                        <el-radio-group v-model="webEngineForm.high_nday_check">
                            <el-radio label="block">阻断请求</el-radio>

                            <el-radio label="reject_response">拒绝响应</el-radio>
                            <el-radio label="watch">观察</el-radio>
                            <el-radio label="close">关闭</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-form>
                <div class="margin-4x"></div>
                <el-row type="flex" class="row-bg" justify="space-between">
                    <el-col :span="24">
                        <el-row type="flex" class="row-bg" justify="space-between">
                            <el-col :span="12">
                                <a class="el-button el-button--primary is-plain" v-if="ruleType == 'single_rule'"
                                    :href="'/#/protection/' + domain + '/' + ruleType">返回</a>
                                <a class="el-button el-button--primary is-plain" v-if="ruleType == 'group_rule'"
                                    :href="'/#/group-protection/' + domain ">返回</a>
                            </el-col>
                            <el-col :span="12" class="text-align-right">
                                <el-button type="primary" @click="onClickPageOwaspSubmit('webEngineForm')"
                                    :loading="loading">保存</el-button>
                            </el-col>
                        </el-row>
                    </el-col>
                </el-row>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import { mixin, JXAjax } from "../assets/scripts/common";
import { useRoute } from 'vue-router'
export default {
    mixins: [mixin],
    data() {
        return {
            loadingPage: false,
            loading: false,
            ruleType: this.$route.params.ruleType,
            domain: this.$route.params.domainUrl,
            webEngineForm: {},

            fileList: [],
            fileListVisible: false,
            fileListValue: "",
            flagTag: true,
        };
    },
    computed: {
        rules() {
            return {

            }
        }
    },

    mounted() {
        const route = useRoute()
        this.ruleType = route.params.ruleType;
        this.domain = route.params.domain;
        this.getData();
    },
    methods: {
        getData() {
            
            var t = this;
            var getUrl = "/waf/waf_get_web_engine_protection";
            var oData = { domain: t.domain };
            if (t.ruleType == "group_rule") { 
                getUrl = "/waf/waf_get_group_web_engine_protection";
                oData = {group_id: t.domain}
            }
            JXAjax(
                "post",
                getUrl,
                oData,
                function (response) {
                    t.loadingPage = false;
                    t.webEngineForm = response.data.message;
                },
                function () {
                    t.loadingPage = false;
                },
                "no-message"
            );
        },

        onClickPageOwaspSubmit(webEngineForm) {
            var t = this;
            var postUrl = "/waf/waf_edit_web_engine_protection";
            if (t.ruleType == "group_rule") {
                postUrl = "/waf/waf_edit_group_web_engine_protection";
                this.webEngineForm.group_id = t.domain;
            } else { 
                this.webEngineForm.domain = t.domain;
            }
            
            this.$refs[webEngineForm].validate((valid) => {
                if (valid) {
                    t.loading = true;
                    JXAjax(
                        "post",
                        postUrl,
                        t.webEngineForm,
                        function (response) {
                            t.loading = false;
                            t.getData();
                        },
                        function () {
                            t.loading = false;
                        }
                    );
                }
            });
        },
    },
};
</script>
<style>



.page-owasp-wrap {
    max-width: 800px;
    min-width: 400px;
}

.page-owasp-wrap .match-inline-block {
    width: 192px;
}
.engine-form .el-form-item__content{
   margin-left: 40px;
}
</style>
